import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, Loading  } from 'ionic-angular';
import {Validators, FormBuilder, FormGroup } from '@angular/forms';

import { NouveautesPage } from '../nouveautes/nouveautes';
import { ApiService } from '../../providers/apiservice';
import { ProfPlanningPage } from '../prof-planning/prof-planning';


@Component({
  selector: 'page-mise-a-jour',
  templateUrl: 'mise-a-jour.html',
})


export class MiseAJourPage {

  _result: any;
  loading: Loading;
  loadingBlur: any;

  constructor(
      public navCtrl: NavController,
      public navParams: NavParams,
      private apiSerivce: ApiService,
      public loadingCtrl: LoadingController,
      private formBuilder: FormBuilder
  ) {

  }

  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  getData(): void {
    this.apiSerivce.getData(
      {
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        enseignant_id: ApiService.enseignantId,
        key  : ApiService.keyToken,
      }, 'mise-a-jour')
        .subscribe(
            result => {
              this._result = result;
              this.loading.dismiss();
            }
              ,
            error => console.log("Erreur :: " + error)
        )
  }

  ngOnInit(): void {
     this.getData();
  }

  closeModal(){
    if(ApiService.parentId) {
      this.navCtrl.setRoot(NouveautesPage);
    } else if(ApiService.enseignantId) {
      this.navCtrl.setRoot(ProfPlanningPage);
    }
  }
  
  
}



