import { Component } from '@angular/core';
import { NavController, NavParams, AlertController,  LoadingController, Loading, Platform} from 'ionic-angular';
import { ApiService } from '../../providers/apiservice';
import { YoutubeVideoPlayer } from '@ionic-native/youtube-video-player';

import { File } from '@ionic-native/file';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { AndroidPermissions } from '@ionic-native/android-permissions';

@Component({
  selector: 'page-cours',
  templateUrl: 'cours.html',
})

export class CoursPage {

  eleve:any;
  currentJustify = 'justified';
  results: any[]=[];
  loading: Loading;
  private fileTransfer: FileTransferObject;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private apiSerivce: ApiService,
    public loadingCtrl: LoadingController,
    public platform: Platform,
    private file: File,
    public alertCtrl: AlertController,
    private androidPermissions: AndroidPermissions,
    private transfer: FileTransfer,
    public youtube: YoutubeVideoPlayer
  ) {
    this.eleve = ApiService.activeEleve;
  }
  

  ionViewWillEnter()
  {
   this.load();
  }

  load()
  {

    
    this.presentLoadingCustom();

    this.apiSerivce.getData(
      { 
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
      }, 'cours')
        .subscribe(
            results => {
              this.results = results;
              this.loading.dismiss();
            }
              , 
            error => console.log("Error :: " + error)
    )
  }

  next(next_date)
  {

    
    this.presentLoadingCustom();
    this.apiSerivce.getData(
      { 
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
        next_week  : next_date
      }, 'cours')
        .subscribe(
            results => {
              this.results = results;
              this.loading.dismiss();
            }
              , 
            error => console.log("Error :: " + error)
    )
  }

  prev(prev_date)
  {

    
    this.presentLoadingCustom();
    this.apiSerivce.getData(
      { 
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
        last_week  : prev_date
      }, 'cours')
        .subscribe(
            results => {
              this.results = results;
              this.loading.dismiss();
            }
              , 
            error => console.log("Error :: " + error)
    )
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  openYoutube(id){
    this.youtube.openVideo(id);
  }

  public downloadFile(fileName, filePath) {  
    //here encoding path as encodeURI() format.  
    let url = encodeURI(filePath);  
    
    this.fileTransfer = this.transfer.create();
    
    let directory = '';
    if (this.platform.is('cordova')) {
      directory = this.file.externalRootDirectory + '/Download/';
    } else {
      directory = this.file.documentsDirectory;
    }


    this.androidPermissions.requestPermissions([
        this.androidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE
    ]).then((result) => {
      // here iam mentioned this line this.file.externalRootDirectory is a native pre-defined file path storage. You can change a file path whatever pre-defined method.  
      this.fileTransfer.download(url, directory  + fileName, true).then((entry) => {  
        //here logging our success downloaded file path in mobile.  
        const alertSuccess = this.alertCtrl.create({
          title: `Téléchargement réussi !`,
          cssClass: 'notification-alert',
          message: `${fileName} a été téléchargé avec succès sur : ${entry.toURL()}`,
          buttons: ['Ok']
        });
    
        alertSuccess.present();

    }, (error) => {  
        //here logging our error its easier to find out what type of error occured.  
        console.log('download failed: ' + error);  
    });  
    });
    
  } 

}



