import { Component } from '@angular/core';
import { NavController, NavParams, Loading, LoadingController } from 'ionic-angular';

import { ApiService } from '../../providers/apiservice';
import { ProfExamensDetailsPage } from '../prof-examens-details/prof-examens-details';

@Component({
  selector: 'page-prof-examens',
  templateUrl: 'prof-examens.html',
})
export class ProfExamensPage {

  _result: any;
  loading: Loading;
  type: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private apiSerivce: ApiService,
    public loadingCtrl: LoadingController
  ) {
  }
  
  getData(): void {
    this.apiSerivce.getData(
      {
        enseignant_id : ApiService.enseignantId,
        key  : ApiService.keyToken
      }, 'prof_examens')
        .subscribe(
            result => {
              this._result = result;
              //this.loading.dismiss();
            },
            error => console.log("Erreur :: " + error)
        )
  }

  ngOnInit(): void {
      this.getData();
  }

  ionViewDidLoad() {
    //this.presentLoadingCustom();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }


  examenDetails(result) {
    this.navCtrl.push(ProfExamensDetailsPage,{
      examen: result
    });
  }


}


