import { Component } from '@angular/core';
import { NavController, ViewController, NavParams, ToastController, LoadingController } from 'ionic-angular';
import {Validators, FormBuilder, FormGroup } from '@angular/forms';

import { ApiService } from '../../providers/apiservice';

@Component({
  selector: 'page-connexion-phonenumber',
  templateUrl: 'connexion-phonenumber.html'
})
export class ConnexionPhoneNumberPage {


  formGroup : FormGroup;
  tel : any;
  code : any;
  stat : any;
  _result: any;
  _resultPage: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public toastCtrl : ToastController,
    private apiSerivce: ApiService,
    public loadingCtrl: LoadingController,
    private formBuilder: FormBuilder,
    public viewCtrl: ViewController
  ) {

    this.formGroup = this.formBuilder.group({
      tel: ['', Validators.required],
      code: ['', Validators.nullValidator]
    });

    this.stat = '1';

  }

  closeModal() {
    this.viewCtrl.dismiss();
  }

  getData(): void {
    this.apiSerivce.getData({}, 'connexion_phonenumber')
        .subscribe(
            result => {
              this._resultPage = result;
            }
              ,
            error => console.log("Erreur :: " + error)
        )
  }

  ngOnInit(): void {
    this.getData();
  }

  valider() {
    /*
    this.apiSerivce.postData("tel=" + this.tel, 'connexion_phonenumber').subscribe(
        result => { 
      
          this._result = result;

          if(this._result.error === false)
          {

            this.sendNotification(this._result.msg, 'toast-success');
            this.viewCtrl.dismiss();

            this.stat = '2';

          }
          else
          {
            this.sendNotification(this._result.msg, 'toast-error');
            this.tel = null;

            
            this.stat = '1';
          }

        }, 
        error =>  {

        }
    );
    */
   
   this.sendNotification('Votre code secret...', 'toast-success');
   this.stat = '2';
  }

  sendNotification(message, classe)  : void
  {
     let notification = this.toastCtrl.create({
         message 		: message,
         duration 		: 100000,
         cssClass: classe,
		 position: 'middle',
		 showCloseButton: true,
		 closeButtonText :'Ok'
     });
     notification.present();
  }

  

}



