import { Component, ElementRef } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { NouvelleDemandePage } from '../nouvelle-demande/nouvelle-demande';
//declare var $: any;


@Component({
  selector: 'page-details-demande',
  templateUrl: 'details-demande.html',
})
export class DetailsDemandePage {
demande:any[]=[];
  constructor(public navCtrl: NavController, public navParams: NavParams,  private elRef: ElementRef) {
    this.demande=this.navParams.get('demande');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DetailsDemandePage');
  }
  private jQueryInit() {
   /* var self = this; */
   /*
    $(this.elRef.nativeElement).ready(function() {
    });
   
   $('nav').scroll(function(){

      if($(document).offsetTop > 0){
          alert('test');
      }
   });

   */

  }
  ngAfterViewInit() {
    this.jQueryInit();
  }
  nouvelle_demande(){
     this.navCtrl.push(NouvelleDemandePage);
  }
  
  getColor(etat){
    switch(etat) { 
      case 'En Cours': { 
          return "en-cours";
          
      } 
      case 'Traitée': { 
          return "cloturee"; 
        
      } 
      case 'Bloquée': { 
          return "refusee"; 
      } 
      case 'Nouvelle': { 
          return "nouvelle";
      } 
      case 'Accepté': { 
          return "accepte";
      }
      case 'Livrée': { 
          return "livree";
      }
      default: { 
        return "en-cours";
      } 
   }
  }
}



