import { Component } from '@angular/core';
import { NavController, NavParams, Loading, LoadingController } from 'ionic-angular';


import { NouvelleDemandePage } from './nouvelle-demande/nouvelle-demande';
import { DetailsDemandePage } from './details-demande/details-demande';
import { ApiService } from '../../providers/apiservice';

@Component({
  selector: 'page-demandes',
  templateUrl: 'demandes.html',
})
export class DemandesPage {

  eleve:any;
  _result: any;
  loading: Loading;

  constructor(
  public navCtrl: NavController,
  public navParams: NavParams,
  private apiSerivce: ApiService,
  public loadingCtrl: LoadingController
  ) {
    this.eleve = ApiService.activeEleve;
  }

  getData(): void {
    this.apiSerivce.getData(
      {
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
      }, 'demandes')
        .subscribe(
            result => {
              this._result = result;
              this.loading.dismiss();
            }
              ,
            error => console.log("Erreur :: " + error)
        )
  }

  ngOnInit(): void {
      this.getData();
  }

  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  nouvelleDemande() {
    this.navCtrl.push(NouvelleDemandePage);
  }


  detail_demande(demande){
    this.navCtrl.push(DetailsDemandePage,{demande:demande});
  }

  getColor(etat){
    switch(etat) { 
      case 'En Cours': { 
          return "en-cours";
          
      } 
      case 'Traitée': { 
          return "cloturee"; 
        
      } 
      case 'Bloquée': { 
          return "refusee"; 
      } 
      case 'Nouvelle': { 
          return "nouvelle";
      } 
      case 'Accepté': { 
          return "accepte";
      }
      case 'Livrée': { 
          return "livree";
      }
      default: { 
        return "en-cours";
      } 
   }
  }

}



