import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';


import { ApiService } from '../../providers/apiservice';

@Component({
  selector: 'page-examens',
  templateUrl: 'examens.html',
})
export class ExamensPage {

  _result: any;
  eleve:any;

  constructor(
  public navCtrl: NavController,
  public navParams: NavParams,
  private apiSerivce: ApiService
  ) {
    this.eleve = ApiService.activeEleve;
  }

  getData(): void {
    this.apiSerivce.getData(
      { 
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
      }, 'examens')
        .subscribe(
            result => { 
              this._result = result;
            }
              , 
            error => console.log("Error :: " + error)
        )
  }

  ngOnInit(): void {
      this.getData();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Examens & NotesPage');
  }
  

}



