import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, AlertController,MenuController, Loading, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Storage } from '@ionic/storage';
import { AppVersion } from '@ionic-native/app-version';
import { Network } from '@ionic-native/network';
import { tap } from 'rxjs/operators';

import { HomePage } from '../pages/home/home';
import { ConnexionPage } from '../pages/connexion/connexion';
import { SuiviPedagogiqueTabsPage } from '../pages/suivipedagogique-tabs/suivipedagogique-tabs';
import { MessagesPage } from '../pages/messages/messages';
import { ComptePage } from '../pages/compte/compte';
import { DemandesPage } from '../pages/demandes/demandes';
import { RessourcesPage } from '../pages/ressources/ressources';
import { DevoirsPage } from '../pages/devoirs/devoirs';
import { NouveautesPage } from '../pages/nouveautes/nouveautes';
import { ContactPage } from '../pages/contact/contact';
import { MiseAJourPage } from '../pages/mise-a-jour/mise-a-jour';
import { NoNetworkPage } from '../pages/no-network/no-network';
import { EtatPaiementPage } from '../pages/etat-paiement/etat-paiement';
import { ApiService } from '../providers/apiservice';
import { FcmProvider } from '../providers/fcm/fcm';
import { DisciplinePage } from '../pages/discipline/discipline';
import { AbsencesPage } from '../pages/absences/absences';
import { ProfPlanningPage } from '../pages/prof-planning/prof-planning';
import { ProfDevoirFormPage } from '../pages/prof-devoir-form/prof-devoir-form';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any;
  loading: Loading;
  activePage:any;
  comOnly:any;
  _resultCompte :any;
  static doubleAccount:any;
  static parent : any;
  static enseignant : any;
  static eleve : any;
  static eleves : any;
  onDev : any = true;
  versionAr : any = ApiService.versionAr;

  static pages: Array<{title: string, visible: boolean, component: any,icone:any}>;

  constructor(
      public platform: Platform,
      public statusBar: StatusBar,
      public fcm: FcmProvider,
      public splashScreen: SplashScreen,
      public events: Events,
      private storage: Storage,
      private network: Network,
      private appVersion: AppVersion,
      private apiSerivce: ApiService,
      public menu: MenuController,
      public alertCtrl: AlertController
    ) {

    this.initializeApp();

    //Event pour stocket Token FCM et init Firebase event notification
    events.subscribe('user:fcm', () => {
      if(!this.onDev) {
        if (this.platform.is('cordova')) {
          this.initFcm();
        }
      }
    });

    //Event pour changer le role to parent
    events.subscribe('user:changetoparent', () => {
      this.menu.swipeEnable(true);
      this.nav.setRoot(NouveautesPage);
    });

    events.subscribe('user:changetoprof', () => {
      this.menu.swipeEnable(false);
      this.nav.setRoot(ProfPlanningPage);
    });

    //Event pour detecter le type de connexion
    events.subscribe('network:type', () => {
      if(!this.onDev) {
        let type = this.network.type;
        if(type == "unknown" || type == "none" || type == undefined){
          this.nav.setRoot(NoNetworkPage);
        }
      }
    });

    // Compte CHECK :)
    events.subscribe('compte:check', () => {

      this.apiSerivce.getData({
        user_id : ApiService.userId,
        key  : ApiService.keyToken,
      }, 'compte_check')
        .subscribe(
            result => {

              this._resultCompte = result;
              if(this._resultCompte.disconnect)  {

                ApiService.activeEleve = null;
                ApiService.eleveId = null;

                this.storage.remove('boti_auth');
                this.storage.remove('boti_userId');
                this.storage.remove('boti_eleveId');
                this.storage.remove('boti_parentId');
                this.storage.remove('boti_enseignantId');
                this.storage.remove('boti_keyToken');
                this.storage.remove('boti_activeEleve');
                this.menu.swipeEnable(false);
                this.rootPage=ConnexionPage;
                return;
              }

              this.events.publish('user:fcm');


              this.events.publish('storage:infos', this._resultCompte);

              if (this.platform.is('cordova')) {

                this.appVersion.getVersionNumber().then((versionNumber) => {
                  if(this._resultCompte.version.version_number != versionNumber)
                    this.rootPage= MiseAJourPage;
                  else
                    this.rootPage= this.rootPageComponent(this._resultCompte._acces);
                });
              } else {
                this.rootPage= this.rootPageComponent(this._resultCompte._acces);
              }

            },
            error =>  {
              this.nav.setRoot(NoNetworkPage, {
                error : true
              });
            }
      );

    });


    //Event pour Stocker les infos en storage
    events.subscribe('storage:infos', (_result) => {

      if(_result._acces == 'parent') {
        this.menu.swipeEnable(true);
      } else if(_result._acces == 'enseignant') {
        this.menu.swipeEnable(false);
      }

      if(_result.parentId) {

        ApiService.parentId = _result.parentId;
        MyApp.parent = _result.parent;
        MyApp.eleves = _result.eleves;

      }

      if(_result.enseignantId) {

        ApiService.enseignantId = _result.enseignantId;
        MyApp.enseignant = _result.enseignant;

      }

      if(_result.parentId && _result.enseignantId)
        MyApp.doubleAccount = true;

      ApiService.keyToken = _result.keyToken;
      ApiService.userId = _result.userId;


      if(!_result.comOnly)
        MyApp.pages[6].visible = true;


      this.storage.set('boti_auth', "boti_connected");
      this.storage.set('boti_parentId', _result.parentId);
      this.storage.set('boti_keyToken', _result.keyToken);
      this.storage.set('boti_userId', _result.userId);

      if(ApiService.eleveId) {

        let currentEleve = MyApp.eleves.find(function (obj) { return obj.id === ApiService.eleveId; });

        ApiService.activeEleve = currentEleve;
        MyApp.eleve = currentEleve;
        this.storage.set('boti_activeEleve', currentEleve);

      } else if(ApiService.parentId) {

        ApiService.eleveId = _result.eleveId;
        ApiService.activeEleve = _result.eleves[0];

        MyApp.eleve = _result.eleves[0];

        this.storage.set('boti_eleveId', _result.eleveId);
        this.storage.set('boti_activeEleve', _result.eleves[0]);
      }
    });

    //Event pour detecter si le user est connecté ou non
     this.storage.get('boti_auth').then((auth) => {
       if(auth=="boti_connected"){
          this.storage.get('boti_userId').then((valeur) => {
             ApiService.userId = valeur;
             this.storage.get('boti_keyToken').then((valeur) => {
              ApiService.keyToken = valeur;

              this.events.publish('compte:check');

           });
          });
       }else{

        this.rootPage=HomePage;
        this.menu.swipeEnable(false);
      }
       this.splashScreen.hide();

     });

     if(ApiService.versionAr) {
          // used for an example of ngFor and navigation
          MyApp.pages = [
            { title: ' المستجدات', visible : true, component: NouveautesPage, icone:'ios-globe-outline' },
            { title: 'التمارين المنزلية',visible : true, component: DevoirsPage, icone:'ios-paper-outline' },
            { title: 'التتبع الدراسي',visible : true, component: SuiviPedagogiqueTabsPage, icone:'ios-school-outline' },
            { title: 'الموارد المدرسية',visible : true, component: RessourcesPage, icone:'ios-send-outline' },
            { title: 'الرسائل',visible : true, component: MessagesPage, icone:'ios-mail-outline' },
            { title: 'الطلبات',visible : true, component: DemandesPage, icone:'ios-create-outline' },
            { title: 'الأداء المالي',visible : false, component: EtatPaiementPage, icone:'ios-card' },
            { title: 'الحساب الشخصي',visible : true, component: ComptePage, icone:'ios-person-outline' },
            { title: 'التواصل',visible : true, component: ContactPage, icone:'ios-information-circle-outline' },

          ];
     } else {


      // used for an example of ngFor and navigation
      MyApp.pages = [
        { title: 'Nouveautés', visible : true, component: NouveautesPage, icone:'ios-globe-outline' },
        { title: 'Devoirs',visible : true, component: DevoirsPage, icone:'ios-paper-outline' },
        { title: 'Suivi pédagogique',visible : true, component: SuiviPedagogiqueTabsPage, icone:'ios-school-outline' },
        { title: 'Ressources',visible : true, component: RessourcesPage, icone:'ios-send-outline' },
        { title: 'Messages',visible : true, component: MessagesPage, icone:'ios-mail-outline' },
        { title: 'Demandes',visible : true, component: DemandesPage, icone:'ios-create-outline' },
        { title: 'Etat de paiement',visible : false, component: EtatPaiementPage, icone:'ios-card' },
        { title: 'Mon compte',visible : true, component: ComptePage, icone:'ios-person-outline' },
        { title: 'Contact',visible : true, component: ContactPage, icone:'ios-information-circle-outline' },

      ];
     }

  }

  get eleve() {
    return MyApp.eleve;
  }

  get doubleAccount() {
    return MyApp.doubleAccount;
  }

  get eleves() {
    return MyApp.eleves;
  }

  get pages() {
    return MyApp.pages;
  }

  public rootPageComponent(_acces) {
    if(_acces == 'parent') {
      return NouveautesPage
    } else if(_acces == 'enseignant') {
      return ProfPlanningPage;
    }
  }

  public switchToProf() {
    this.events.publish('user:changetoprof');
  }

  public initFcm() {
     // Get a FCM token
     this.fcm.getToken()
     this.fcm.listenToNotifications().pipe(
       tap(msg => {
        NouveautesPage.newNotif = true;
        if(msg.page) {
          let pageTo = msg.page;
          if(pageTo == 'eleve_absences')
            this.nav.setRoot(AbsencesPage);
          else if(pageTo == 'eleve_nouveautes')
            this.nav.setRoot(NouveautesPage);
          else if(pageTo == 'eleve_paiements')
            this.nav.setRoot(EtatPaiementPage);
          else if(pageTo == 'eleve_devoirs')
            this.nav.setRoot(DevoirsPage);
          else if(pageTo == 'eleve_demandes')
            this.nav.setRoot(DemandesPage);
          else if(pageTo == 'eleve_messages')
            this.nav.setRoot(MessagesPage);
          else if(pageTo == 'eleve_emploi')
            this.nav.setRoot(SuiviPedagogiqueTabsPage, {
              tabIndex : 0
            });
          else if(pageTo == 'eleve_absences')
            this.nav.setRoot(SuiviPedagogiqueTabsPage, {
              tabIndex : 1
            });
          else if(pageTo == 'eleve_examens')
            this.nav.setRoot(SuiviPedagogiqueTabsPage, {
              tabIndex : 2
            });
          else if(pageTo == 'eleve_discipline')
            this.nav.setRoot(SuiviPedagogiqueTabsPage, {
              tabIndex : 3
            });


        }
       })
     ).subscribe()
  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      //this.statusBar.backgroundColorByHexString('#ffb606');
      this.listenConnection();
    });
  }

  private listenConnection(): void {
    if(!this.onDev) {
      let type = this.network.type;
      if(type == "unknown" || type == "none" || type == undefined){
        this.rootPage = NoNetworkPage;
        this.nav.setRoot(NoNetworkPage);
      }
      this.network.onDisconnect()
        .subscribe(() => {
          this.nav.setRoot(NoNetworkPage);
      });
    }
  }

  changeprofil(eleve) {
    this.activePage = null;

    ApiService.activeEleve = eleve;
    ApiService.eleveId = eleve.id;

    this.storage.set('activeleve',eleve);
    this.storage.set('boti_eleveId', eleve.id);
    this.storage.set('boti_activeEleve', eleve);

    MyApp.eleve = eleve;
    this.menu.close();

    this.nav.setRoot(NouveautesPage);

  }

  logout(){


    ApiService.activeEleve = null;
    ApiService.eleveId = null;
    ApiService.enseignantId = null;

    this.storage.remove('boti_auth');
    this.storage.remove('boti_userId');
    this.storage.remove('boti_eleveId');
    this.storage.remove('boti_parentId');
    this.storage.remove('boti_enseignantId');
    this.storage.remove('boti_keyToken');
    this.storage.remove('boti_activeEleve');
    this.menu.swipeEnable(false);
    this.nav.setRoot(HomePage);
  }

  checkActive(page){
    return page == this.activePage;
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.activePage=page;
    this.nav.setRoot(page.component);
  }
}



