import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Loading, Events, LoadingController } from 'ionic-angular';
import * as moment from 'moment';
import { DatePipe } from '@angular/common';
import { tap } from 'rxjs/operators';

import { FcmProvider } from '../../providers/fcm/fcm';
import { ApiService } from '../../providers/apiservice';
import { PostPage } from '../../pages/post/post';
import { NotificationsPage } from '../../pages/notifications/notifications';
import { SuiviPedagogiqueTabsPage } from '../suivipedagogique-tabs/suivipedagogique-tabs';

@Component({
  selector: 'page-nouveautes',
  templateUrl: 'nouveautes.html',
})
export class NouveautesPage {

  eleve:any;
  _result: any;
  loading: Loading;
  static newNotif: any;
  
  constructor(
  public navCtrl: NavController,
  public navParams: NavParams,
  public events: Events,
  public fcm: FcmProvider,
  private apiSerivce: ApiService,
  private datePipe: DatePipe,
  public loadingCtrl: LoadingController
  ) {
    
    moment.locale('fr');
    this.eleve = ApiService.activeEleve;
  }

  getData(): void {
    console.log(ApiService);
    this.apiSerivce.getData(
      {
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
      }, 'nouveautes')
        .subscribe(
            result => {
              this._result = result;
              this.loading.dismiss();
            }
              ,
            error => console.log("Erreur :: " + error)
        );

    setTimeout(()=>{
        this.events.publish('network:type');
    }, 10000);
  }

  ngOnInit(): void {
    this.getData();
  }

   
  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  post(post) {
    this.navCtrl.push(PostPage, {
      result : post
    });
  }

  get newNotif() {
    return NouveautesPage.newNotif;
  }

  notifications() {
    this.navCtrl.push(NotificationsPage);
  }


}



