import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController, AlertController } from 'ionic-angular';
import {Validators, FormBuilder, FormControl,  FormGroup } from '@angular/forms';

import { ProfMessagesPage } from '../prof-messages';
import { ApiService } from '../../../providers/apiservice';
import { ProfPlanningPage } from '../../prof-planning/prof-planning';


@Component({
  selector: 'page-prof-nouveau-message',
  templateUrl: 'prof-nouveau-message.html',
})
export class ProfNouveauMessagePage {
  imageURI:any;
  imageFileName:any;
  public translation 	: any;
  public sujet 	: any;
  public message	: any;
  public messageRef	: any;
  private formGroup : FormGroup;

  constructor(public navCtrl: NavController,
              public loadingCtrl: LoadingController,
              private alertCtrl: AlertController,
              public toastCtrl: ToastController,
              private apiSerivce: ApiService,
              public navParams: NavParams,
              private formBuilder: FormBuilder
  ) {

    this.formGroup = this.formBuilder.group({
      sujet: ['', Validators.required],
      message: ['', Validators.required],
    });

    this.messageRef = navParams.get('messageRef');
    this.sujet = navParams.get('sujet');
    this.translation = navParams.get('translation');

  }


    ionViewDidLoad() {

    }

    send()
    {
      let query = "sujet=" + this.sujet + 
                  "&message=" + this.message + 
                  "&enseignant_id="+ApiService.enseignantId + 
                  "&key="+ApiService.keyToken 
                  ;

      if(this.messageRef)
        query += "&ref="+this.messageRef;

        this.apiSerivce.postData(query,  'prof_messages').subscribe(
          result => { 

            let res: any = result;
            this.navCtrl.setRoot(ProfPlanningPage);
            let alert = this.alertCtrl.create({
              cssClass: 'success_alert_boti',
              title: res.title,
              message: res.message,
              buttons: [{
                text: 'Fermer',
                role: 'cancel',
                handler: () => {
                }
              }]
            });
            alert.present();
    
          }, 
          error =>  {
    
            //alert(error);
            console.log("Error :: " + error);
            //this.loading.dismiss().catch();
          }
      );
    }

  }



