import { Component, ElementRef, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform, AlertController  } from 'ionic-angular';
import { DomSanitizer} from '@angular/platform-browser';
import * as moment from 'moment';
import { DatePipe } from '@angular/common';

import { File } from '@ionic-native/file';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { InAppBrowser } from '@ionic-native/in-app-browser';

import { ApiService } from '../../providers/apiservice';
import {Validators, FormBuilder, FormControl,  FormGroup } from '@angular/forms';


@Component({
  selector: 'page-post',
  templateUrl: 'post.html',
})
export class PostPage {

  result:any;
  _result:any;
  _resultQuiz:any;
  description:any;
  eleve:any;
  fileTransfer: FileTransferObject = this.transfer.create();
  public commentaire	: any;
  private formGroup : FormGroup;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private iab: InAppBrowser,
    private formBuilder: FormBuilder,
    private datePipe: DatePipe,
    public alertCtrl: AlertController,
    private apiSerivce: ApiService,
    private sanitizer: DomSanitizer,
    private file: File,
    private transfer: FileTransfer,
    private platform: Platform
  ) {

      moment.locale('fr');
      this.eleve = ApiService.activeEleve;
      this.result = navParams.get('result');
      this.description = this.getInnerHTMLValue();

      this.formGroup = this.formBuilder.group({
        commentaire: ['', Validators.required],
      });
  }

  getData(): void {
    this.apiSerivce.getData(
      {
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
        post  : this.result.id
      }, 'post_view')
        .subscribe(
            result => {
              this._result = result;
            }
              ,
            error => console.log("Erreur :: " + error)
        )
  }

  ngOnInit(): void {
    this.getData();
 }

  getInnerHTMLValue(){
    return this.sanitizer.bypassSecurityTrustHtml(this.result.description);
  }

  ionViewDidLoad() {
  }

  downloadFile(file) {
    const browser = this.iab.create(file.link);
  }

  send()
  {
    let query = "commentaire=" + this.commentaire + 
                "&eleve_id="+ApiService.eleveId + 
                "&parent_id="+ApiService.parentId +
                "&key="+ApiService.keyToken 
                ;

      query += "&post="+ this.result.id;

      if(!ApiService.urlLoadingPost)
      this.apiSerivce.postData(query,  'nouveautes').subscribe(
        result => { 

          this.navCtrl.setRoot(PostPage,{result: this.result});
  
        }, 
        error =>  {
  
          //alert(error);
          console.log("Error :: " + error);
          //this.loading.dismiss().catch();
        }
    );
  }

  send_quiz(reponse)
  {
    let query = "quiz=" + reponse + 
                "&eleve_id="+ApiService.eleveId + 
                "&parent_id="+ApiService.parentId +
                "&key="+ApiService.keyToken 
                ;

      query += "&post="+ this.result.id;

      if(!ApiService.urlLoadingPost)
      this.apiSerivce.postData(query,  'nouveautes').subscribe(
        result => {
          
          this._resultQuiz = result;
          this.result.quizDone = this._resultQuiz.quizDone;
          this.result.permitQuiz = false;
          const alertSuccess = this.alertCtrl.create({
            title: 'Merci !',
            message: this._resultQuiz.message,
            cssClass: 'success_alert_boti',
            buttons: ['Ok']
          });
      
          alertSuccess.present();
  
        }, 
        error =>  {
  
          //alert(error);
          console.log("Error :: " + error);
          //this.loading.dismiss().catch();
        }
    );
  }

}



