import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, Loading } from 'ionic-angular';
import {Validators, FormBuilder, FormControl,  FormGroup } from '@angular/forms';

import { Camera, CameraOptions } from '@ionic-native/camera';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';

import { ComptePage } from '../compte/compte';
import { ApiService } from '../../providers/apiservice';

@Component({
  selector: 'page-compte-eleve',
  templateUrl: 'compte-eleve.html',
})
export class CompteElevePage {

  eleve:any;
  _result: any;
  loading: Loading;
  private formGroup : FormGroup;

  nom:any;
  loadingBlur: any;
  prenom:any;
  nomar:any;
  prenomar:any;
  email:any;
  tel:any;
  datenaissance:any;
  sexe:any;
  photo:any;

  constructor(
  public navCtrl: NavController,
  private camera: Camera, 
  private transfer: FileTransfer,
  private file: File,
  public navParams: NavParams,
  private apiSerivce: ApiService,
  public loadingCtrl: LoadingController,
  private formBuilder: FormBuilder
  ) {

	
    if(this.navParams.get('eleve'))
      this.eleve = this.navParams.get('eleve');
  
    this.formGroup = this.formBuilder.group({
      sexe : ['', Validators.required],
      nom : ['', Validators.required],
      prenom : ['', Validators.required],
      nomar : ['', Validators.nullValidator],
      prenomar : ['', Validators.nullValidator],
      datenaissance : ['', Validators.required],
    });

    this.datenaissance = this.eleve.datenaissance;
    if(this.eleve.nom)
      this.nom = this.eleve.nom;
    if(this.eleve.prenom)
      this.prenom = this.eleve.prenom;
    if(this.eleve.nomar)
      this.nomar = this.eleve.nomar;
    if(this.eleve.prenomar)
      this.prenomar = this.eleve.prenomar;
    if(this.eleve.img)
      this.photo = this.eleve.img;
    if(this.eleve.sexe)
      this.sexe = this.eleve.sexe;
  }

  
  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  getData(): void {
    this.apiSerivce.getData({
       parent_id : ApiService.parentId,
       key  : ApiService.keyToken,
      }, 'compte-eleve')
        .subscribe(
            result => { 
              this._result = result;

              this.loading.dismiss();
              this.loadingBlur = null;
            }, 
            error =>  {
              console.log("Error :: " + error);
              this.loading.dismiss();
            }
        )
  }

  ngOnInit(): void {
    this.getData();
  }

  cropImage() {
    const options: CameraOptions = {
      quality: 70,
      destinationType: this.camera.DestinationType.DATA_URL,
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      saveToPhotoAlbum: false,
      allowEdit:true,
      targetWidth:400,
      targetHeight:400
    }

    this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      // If it's base64:
      this.photo = 'data:image/jpeg;base64,' + imageData;
    }, (err) => {
      // Handle error
    });
  }

  send()
    {
      let query = "datenaissance=" + this.datenaissance + 
                  "&sexe=" + this.sexe + 
                  "&eleve_id="+this.eleve.id + 
                  "&parent_id="+ApiService.parentId +
                  "&key="+ApiService.keyToken 
                  ;


        if(this.nom)
          query += "&nom="+this.nom;
        if(this.prenom)
          query += "&prenom="+this.prenom;
        if(this.nomar)
          query += "&nomar="+this.nomar;
        if(this.prenomar)
          query += "&prenomar="+this.prenomar;

        if(this.photo != this.eleve.img)
          query += "&photo="+this.photo;
        
        this.apiSerivce.postData(query,  'compte-eleve').subscribe(
          result => { 

            this.navCtrl.setRoot(ComptePage);
    
          }, 
          error =>  {
    
            //alert(error);
            console.log("Error :: " + error);
            //this.loading.dismiss().catch();
          }
      );
    }

  post() {
    
  }


}



