import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ProfNouveauMessagePage } from '../prof-nouveau-message/prof-nouveau-message';
import {Validators, FormBuilder, FormControl,  FormGroup } from '@angular/forms';


import { ProfMessagesPage } from '../prof-messages';
import { ApiService } from '../../../providers/apiservice';
import { ProfPlanningPage } from '../../prof-planning/prof-planning';

@Component({
  selector: 'page-prof-conversation',
  templateUrl: 'prof-conversation.html',
})
export class ProfConversationPage {

  reference:any;
  _result:any;
  public message	: any;
  private formGroup : FormGroup;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private apiSerivce: ApiService,
    private formBuilder: FormBuilder
  ) {

    this.reference = navParams.get('reference');
    console.log(this.reference);
    this._result = this.reference.conversation;
    this.formGroup = this.formBuilder.group({
      message: ['', Validators.required],
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DetailsMessagePage');
  }

  getData(): void {
    this.apiSerivce.getData(
      { 
        reference : this.reference.id,
        enseignant_id : ApiService.enseignantId,
        key  : ApiService.keyToken,
      }, 'messages')
        .subscribe(
            result => { 
              this._result = result;
            }, 
            error => console.log("Error :: " + error)
        )
  }

  ngOnInit(): void {
     // this.getData();
  }

  nouveauMessage(message, sujet){
    this.navCtrl.push(ProfNouveauMessagePage,{messageRef: message, sujet: sujet, });
  }

  send()
    {
      let query = "sujet=" + '' + 
                  "&message=" + this.message + 
                  "&enseignant_id="+ApiService.enseignantId + 
                  "&key="+ApiService.keyToken 
                  ;

        query += "&ref="+ this.reference.id;

        this.apiSerivce.postData(query,  'prof_messages').subscribe(
          result => { 

            this.navCtrl.setRoot(ProfPlanningPage);
    
          }, 
          error =>  {
    
            //alert(error);
            console.log("Error :: " + error);
            //this.loading.dismiss().catch();
          }
      );
    }
}



