import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import * as moment from 'moment';


import { NouveautesPage } from '../../pages/nouveautes/nouveautes';
import { ApiService } from '../../providers/apiservice';

@Component({
  selector: 'page-notifications',
  templateUrl: 'notifications.html',
})
export class NotificationsPage {

  _result: any;

  constructor(
  public navCtrl: NavController,
  public navParams: NavParams,
  public alertCtrl: AlertController,
  private apiSerivce: ApiService
  ) {

    NouveautesPage.newNotif = false;
    moment.locale('fr');
  }

  getData(): void {
    this.apiSerivce.getData(
      { 
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
      }, 'notifications')
        .subscribe(
            result => { 
              console.log(result);
              this._result = result;
            }, 
            error => console.log("Error :: " + error)
        )
  }

  ngOnInit(): void {
      this.getData();
  }

  notification(notification) {
    let confirmAlert = this.alertCtrl.create({
      title: notification.label,
      message: notification.message,
      cssClass: 'notification-alert',
      buttons: [{
        text: 'Fermer',
        role: 'cancel'
      }]
    });
    confirmAlert.present();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Messages & NotesPage');
  }
  

}



