import { Component } from '@angular/core';
import { NavController, ViewController, Loading, NavParams, ToastController, LoadingController } from 'ionic-angular';
import {Validators, FormBuilder, FormGroup } from '@angular/forms';

import { ApiService } from '../../providers/apiservice';

@Component({
  selector: 'page-forgot-password',
  templateUrl: 'forgot-password.html'
})
export class ForgotPasswordPage {


  formGroup : FormGroup;
  email : any; 
  _result: any;
  _resultPage: any;
  loading: Loading;
  loadingBlur: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public toastCtrl : ToastController,
    private apiSerivce: ApiService,
    public loadingCtrl: LoadingController,
    private formBuilder: FormBuilder,
    public viewCtrl: ViewController
  ) {

    this.formGroup = this.formBuilder.group({
      email: ['', Validators.required]
    });

  }

  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  getData(): void {
    this.apiSerivce.getData({
       parent_id : ApiService.parentId,
       key  : ApiService.keyToken,
      }, 'forgot-password')
        .subscribe(
            result => { 
              this._resultPage = result;

              this.loading.dismiss();
              this.loadingBlur = null;
            }, 
            error =>  {
              console.log("Error :: " + error);
              this.loading.dismiss();
            }
        )
  }

  ngOnInit(): void {
    this.getData();
  }

  closeModal() {
    this.viewCtrl.dismiss();
  }

  valider() {
    this.apiSerivce.postData("email=" + this.email, 'forgot-password').subscribe(
        result => { 
      
          this._result = result;

          if(this._result.error === false)
          {

            this.sendNotification(this._result.msg, 'toast-success');
            this.viewCtrl.dismiss();

          }
          else
          {
            this.sendNotification(this._result.msg, 'toast-error');
            this.email = null;
          }

        }, 
        error =>  {

        }
    );
  }

  sendNotification(message, classe)  : void
  {
     let notification = this.toastCtrl.create({
         message 		: message,
         duration 		: 100000,
         cssClass: classe,
		 position: 'middle',
		 showCloseButton: true,
		 closeButtonText :'Ok'
     });
     notification.present();
  }

  

}



