import { Component, Input } from '@angular/core';
import { NavController, NavParams, LoadingController, Loading } from 'ionic-angular';
import { ApiService } from '../../providers/apiservice';

@Component({
  selector: 'page-etat-paiement',
  templateUrl: 'etat-paiement.html',
})
export class EtatPaiementPage {

  @Input('progress') progress;
  eleve:any;
  _result: any;
  loading: Loading;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private apiSerivce: ApiService,
    public loadingCtrl: LoadingController
  ) {
    this.eleve = ApiService.activeEleve;
  }

  ionViewWillEnter()
  {
    this.getData();

  }

  getData(): void {
    this.apiSerivce.getData(
      { 
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
      }, 'paiements')
        .subscribe(
            results => { 
              this._result = results;
              this.loading.dismiss();
            }
              , 
            error => console.log("Error :: " + error)
        )
  }

  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  progressColor(pctg) {
    if ( pctg == 0 ){
      return '#af222a';
    }else if( pctg < 50 ){
        return '#3e71b2';
    }else{
      return '#e37931';
    }
  }

}



