import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, Loading } from 'ionic-angular';
import {Validators, FormBuilder, FormControl,  FormGroup } from '@angular/forms';

import { ApiService } from '../../../providers/apiservice';
import { DemandesPage } from '../demandes';

@Component({
  selector: 'page-nouvelle-demande',
  templateUrl: 'nouvelle-demande.html',
})
export class NouvelleDemandePage {

  eleve:any;
  _result: any;
  nature:any;
  loading: Loading;
  private formGroup : FormGroup;

  constructor(
  public navCtrl: NavController,
  public navParams: NavParams,
  private apiSerivce: ApiService,
  public loadingCtrl: LoadingController,
  private formBuilder: FormBuilder
  ) {

    if(this.navParams.get('nature'))
      this.nature = this.navParams.get('nature');

      if(this.navParams.get('_result'))
        this._result = this.navParams.get('_result');
    
    if(this.nature) {

      this.formGroup = this.formBuilder.group({
        remarque : ['', Validators.nullValidator],
      });

      this.nature.questions.forEach(element => {
          const control: FormControl = new FormControl('question_'+element.id , element.required?Validators.required:Validators.nullValidator);
          this.formGroup.addControl('question_'+element.id, control);
      });
    }

    this.eleve = ApiService.activeEleve;
  }

  getData(): void {
    this.apiSerivce.getData(
      {
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
      }, 'nouvelle-demande')
        .subscribe(
            result => {
              this._result = result;
              this.loading.dismiss();
            }
              ,
            error => console.log("Erreur :: " + error)
        )
  }

  

  ngOnInit(): void {
      if(!this.nature)
        this.getData();
  }

  ionViewDidLoad() {
    if(!this.nature)
      this.presentLoadingCustom();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  post() {
    
  }

  natureDemande(nature) {
    this.navCtrl.push(NouvelleDemandePage, {
      nature: nature,
      _result: this._result
    });
  }

  send()
    {
      let query = "nature=" + JSON.stringify(this.nature) + 
                  "&eleve_id="+this.eleve.id + 
                  "&parent_id="+ApiService.parentId +
                  "&key="+ApiService.keyToken 
                  ;
        
        this.apiSerivce.postData(query,  'nouvelle-demande').subscribe(
          result => { 

            this.navCtrl.setRoot(DemandesPage);
    
          }, 
          error =>  {
    
            //alert(error);
            console.log("Error :: " + error);
            //this.loading.dismiss().catch();
          }
      );
    }


}



