import { Component } from '@angular/core';
import { NavController, NavParams, Loading, LoadingController, AlertController } from 'ionic-angular';

import { ApiService } from '../../providers/apiservice';
import { ProfPlanningPage } from '../prof-planning/prof-planning';

@Component({
  selector: 'page-prof-examens-details',
  templateUrl: 'prof-examens-details.html',
})
export class ProfExamensDetailsPage {
	
  showPopUp:boolean = false;
  _result: any;
  examen: any;
  loading: Loading;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private alertCtrl: AlertController,
    private apiSerivce: ApiService,
    public loadingCtrl: LoadingController
  ) {
    this.examen = navParams.get('examen');
  }
  
  
  getData(): void {
    this.apiSerivce.getData(
      {
        examen_id : this.examen.id,
        enseignant_id : ApiService.enseignantId,
        key  : ApiService.keyToken,
      }, 'prof_examens_details')
        .subscribe(
            result => {
              this._result = result;
              this.loading.dismiss();
            }
              ,
            error => console.log("Erreur :: " + error)
        )
  }

  ngOnInit(): void {
      this.getData();
  }

  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }


submit() {
	this.apiSerivce.postData("enseignant_id="+ApiService.enseignantId+"&examen_id="+this.examen.id+"&notes=" + JSON.stringify(this._result.data.eleves), 'prof_examens_details').subscribe(
		result => {
       
      let res: any = result;
      this.navCtrl.setRoot(ProfPlanningPage);
      let alert = this.alertCtrl.create({
        cssClass: 'success_alert_boti',
        title: res.title,
        message: res.message,
        buttons: [{
          text: 'Fermer',
          role: 'cancel',
          handler: () => {
          }
        }]
      });
      alert.present();
		}, 
		error =>  {
		  this.loading.dismiss();
		}
	);
}

}



