import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, Loading, ToastController } from 'ionic-angular';
import {Validators, FormBuilder,  FormGroup } from '@angular/forms';

import { Camera, CameraOptions } from '@ionic-native/camera';
import { FileTransfer } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';

import { ComptePage } from '../compte/compte';
import { ApiService } from '../../providers/apiservice';

@Component({
  selector: 'page-compte-parent',
  templateUrl: 'compte-parent.html',
})
export class CompteParentPage {

  parent:any;
  _result: any;
  loading: Loading;
  private formGroup : FormGroup;

  nom:any;
  prenom:any;
  email:any;
  tel:any;
  sexe:any;
  photo:any;

  constructor(
  public navCtrl: NavController,
  private camera: Camera, 
  public navParams: NavParams,
  public toastCtrl : ToastController,
  private apiSerivce: ApiService,
  public loadingCtrl: LoadingController,
  private formBuilder: FormBuilder
  ) {

	
    if(this.navParams.get('parent'))
      this.parent = this.navParams.get('parent');
  
    this.formGroup = this.formBuilder.group({
      sexe : ['', Validators.required],
      nom : ['', Validators.required],
      prenom : ['', Validators.required],
      tel : ['', Validators.required],
      email : ['', Validators.required],
    });

    console.log(this.parent);

    if(this.parent.nom)
      this.nom = this.parent.nom;
    if(this.parent.prenom)
      this.prenom = this.parent.prenom;
    if(this.parent.email)
      this.email = this.parent.email;
    if(this.parent.tel)
      this.tel = this.parent.tel;
    if(this.parent.img)
      this.photo = this.parent.img;
    if(this.parent.sexe)
      this.sexe = this.parent.sexe;
  }

  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  getData(): void {
    this.apiSerivce.getData({
       parent_id : ApiService.parentId,
       key  : ApiService.keyToken,
      }, 'compte-parent')
        .subscribe(
            result => { 
              this._result = result;

              this.loading.dismiss();
            }, 
            error =>  {
              console.log("Error :: " + error);
              this.loading.dismiss();
            }
        )
  }

  ngOnInit(): void {
    this.getData();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  cropImage() {
    const options: CameraOptions = {
      quality: 70,
      destinationType: this.camera.DestinationType.DATA_URL,
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      saveToPhotoAlbum: false,
      allowEdit:true,
      targetWidth:400,
      targetHeight:400
    }

    this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      // If it's base64:
      this.photo = 'data:image/jpeg;base64,' + imageData;
    }, (err) => {
      // Handle error
    });
  }

  send()
    {
      console.log(this.sexe);
      let query = "sexe=" + this.sexe + 
                  "&parent_id="+ApiService.parentId +
                  "&key="+ApiService.keyToken 
                  ;


        if(this.nom)
          query += "&nom="+this.nom;
        if(this.prenom)
          query += "&prenom="+this.prenom;
        if(this.email)
          query += "&email="+this.email;
        if(this.tel)
          query += "&tel="+this.tel;

        if(this.photo != this.parent.img)
          query += "&photo="+this.photo;
        
        this.apiSerivce.postData(query,  'compte-parent').subscribe(
          result => { 

            this._result = result;
            
            if(this._result.error === true) {
                
                this.sendNotification(this._result.msg, 'toast-error');
            }
            else {
              this.navCtrl.setRoot(ComptePage);
            }
    
          }, 
          error =>  {
    
            //alert(error);
            console.log("Error :: " + error);
            //this.loading.dismiss().catch();
          }
      );
    }

  post() {
    
  }

  sendNotification(message, classe)  : void
  {
     let notification = this.toastCtrl.create({
         message 		: message,
         duration 		: 100000,
         cssClass: classe,
		 position: 'middle',
		 showCloseButton: true,
		 closeButtonText :'Ok'
     });
     notification.present();
  }


}



