import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Firebase } from '@ionic-native/firebase';
import { Platform } from 'ionic-angular';
import { ApiService } from '../../providers/apiservice';


@Injectable()
export class FcmProvider {

  constructor(
    public firebaseNative: Firebase,
    private apiSerivce: ApiService,
    private platform: Platform
  ) {}

  async getToken() {
    
    if (!this.platform.is('cordova')) 
      return;
    
    let token;

    if (this.platform.is('android')) {
     
      token = await this.firebaseNative.getToken()
    } 

    if (this.platform.is('ios')) {
      token = await this.firebaseNative.getToken();
      const perm = await this.firebaseNative.grantPermission();
    } 
    
    // Is not cordova == web PWA
    if (!this.platform.is('cordova')) {
      // TODO add PWA support with angularfire2
    } 

    return this.saveTokenToFirestore(token)
  }

  private saveTokenToFirestore(token) {
    if (!token) {
      return;
    }
	  this.apiSerivce.postData("token=" + token+'&parent_id=' + ApiService.parentId +'&enseignant_id=' + ApiService.enseignantId + '&key=' + ApiService.keyToken, 'token').subscribe();
  }

  listenToNotifications() {
    return this.firebaseNative.onNotificationOpen()
  }

}



