import { Component, AfterViewInit, ViewChild, ElementRef, Renderer } from '@angular/core';
import { NavController, AlertController, ModalController, NavParams,  LoadingController, Loading,Events, ToastController,MenuController } from 'ionic-angular';

import { Storage } from '@ionic/storage';
import {Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Sim } from "@ionic-native/sim";

import { ApiService } from '../../providers/apiservice';
import { FcmProvider } from '../../providers/fcm/fcm';
import { MyApp } from '../../app/app.component';
import { NouveautesPage } from '../nouveautes/nouveautes';
import { ForgotPasswordPage } from '../forgot-password/forgot-password';
import { ConnexionPhoneNumberPage } from '../connexion-phonenumber/connexion-phonenumber';
import { ProfPlanningPage } from '../prof-planning/prof-planning';


@Component({
  selector: 'page-connexion',
  templateUrl: 'connexion.html',
})
export class ConnexionPage {

  @ViewChild('passwordInput') passwordInputElement: ElementRef;
  _result: any;
  _resultPage: any;
  loading: Loading;
  loadingBlur: any;
  public loginI 	: any;
  public passwordI	: any;
  private formGroup : FormGroup;
  showPwd: boolean = false;
  pwdType: string = "password";
  pwdIcon: string = "md-eye-off";
  
  public simInfo: any;
  public cards: any;
  
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public modalCtrl: ModalController,
    private sim: Sim,
    public loadingCtrl: LoadingController, 
    public events: Events,
    public fcm: FcmProvider,
    private renderer: Renderer,
    private apiSerivce: ApiService,
    private storage: Storage,
    private formBuilder: FormBuilder,
    public toastCtrl : ToastController,
    public menu: MenuController,
  ) {

    this.formGroup = this.formBuilder.group({
      email: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

  getData(): void {
    this.apiSerivce.getData({}, 'login-page')
        .subscribe(
            result => {
              this._resultPage = result;
            }
              ,
            error => console.log("Erreur :: " + error)
        )
  }

  ngOnInit(): void {
    this.getData();
    //this.getSimData();
    this.events.publish('network:type');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ConnexionPage');
  }

  presentLoadingCustom() {
    this.loading = this.loadingCtrl.create({
    spinner: 'hide',
    content: ApiService.loaderHtml,
    duration: 10000
  });
  this.loading.present();
  }

  async getSimData() {
    try {
      let simPermission = await this.sim.requestReadPermission();
      if (simPermission == "OK") {
        let simData = await this.sim.getSimInfo();
        this.simInfo = simData;
        this.cards = simData.cards

        //alert(JSON.stringify(simData));
        //alert(JSON.stringify(this.simInfo));

        if(this.simInfo.phoneNumber) {
          this.apiSerivce.postData("phone=" + this.simInfo.phoneNumber, 'phone_auth').subscribe(
              result => { 
            
                this._result = result;
        
                if(this._result.error === false)
                {
                
                this.sendNotification(this._result.msg, 'toast-success');
                
                

                this.events.publish('storage:infos', this._result);
                this.events.publish('user:fcm');
        
                if(this._result._acces == 'parent') {
                  this.navCtrl.setRoot(NouveautesPage);
                } else if(this._result._acces == 'enseignant') {
                  this.navCtrl.setRoot(ProfPlanningPage);
                }
        
        
                }
                else
                {
                  this.sendNotification(this._result.msg, 'toast-error');
                }
        
              }, 
              error =>  {
        
                console.log("Error :: " + error);

              }
          );

        }
        //alert('success');
        //alert(JSON.stringify(simData));
      }
    } catch (error) {
      //alert('error');
      //alert(JSON.stringify(error));
    }
  }

  sendNotification(message, classe)  : void
  {
     let notification = this.toastCtrl.create({
         message 		: message,
         duration 		: 100000,
         cssClass: classe,
		 position: 'middle',
		 showCloseButton: true,
		 closeButtonText :'Ok'
     });
     notification.present();
  }

  
  
  showHidePassword()
  {
  this.showPwd = !this.showPwd;
  
  if(this.showPwd){
    this.pwdType = 'text';
    this.pwdIcon = 'md-eye-off';
  } else {
    this.pwdType = 'password';
    this.pwdIcon = 'md-eye';
  }
  }

  login(){
    this.apiSerivce.postData("login=" + this.loginI + "&password=" + this.passwordI, 'login').subscribe(
      result => { 
    
        this._result = result;

        if(this._result.error === false)
        {
        
        this.events.publish('storage:infos', this._result);

        this.events.publish('user:fcm');

        if(this._result._acces == 'parent') {
          this.navCtrl.setRoot(NouveautesPage);
        } else if(this._result._acces == 'enseignant') {
          this.navCtrl.setRoot(ProfPlanningPage);
        }


        }
        else
        {
           this.sendNotification(this._result.msg, 'toast-error');
        }

      }, 
      error =>  {

        //alert(error);
        console.log("Error :: " + error);
        //this.loading.dismiss().catch();
      }
  );
 }

  forgotPasswordModal() {
    let passwordModal = this.modalCtrl.create(ForgotPasswordPage);
    passwordModal.present();
  }

  connexionPhonenumberModal() {
    let passwordModal = this.modalCtrl.create(ConnexionPhoneNumberPage);
    passwordModal.present();
  }

}



