import { Component } from '@angular/core';
import { NavController, NavParams, Loading, LoadingController } from 'ionic-angular';

import { ApiService } from '../../providers/apiservice';
import { ProfDevoirFormPage } from '../prof-devoir-form/prof-devoir-form';
import { ProfDevoirDetailsPage } from '../prof-devoir-details/prof-devoir-details';

@Component({
  selector: 'page-prof-devoirs',
  templateUrl: 'prof-devoirs.html',
})
export class ProfDevoirsPage {

  _result: any;
  loading: Loading;
  type: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private apiSerivce: ApiService,
    public loadingCtrl: LoadingController
  ) {

    this.type = this.navParams.get('type');
  }
  
  getData(): void {
    this.apiSerivce.getData(
      {
        enseignant_id : ApiService.enseignantId,
        key  : ApiService.keyToken,
        type  : this.type
      }, 'prof_contents')
        .subscribe(
            result => {
              this._result = result;
              this.loading.dismiss();
            },
            error => console.log("Erreur :: " + error)
        )
  }

  ngOnInit(): void {
      this.getData();
  }

  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  ajouterDevoir() {
    this.navCtrl.push(ProfDevoirFormPage,{
      type: this.type
    });
  }

  devoirDetails(result) {
    this.navCtrl.push(ProfDevoirDetailsPage,{
      result: result
    });
  }


}


