import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';


import { ApiService } from '../../providers/apiservice';
import { ProfNouveauMessagePage } from './prof-nouveau-message/prof-nouveau-message';
import { ProfConversationPage } from './prof-conversation/prof-conversation';

@Component({
  selector: 'page-prof-messages',
  templateUrl: 'prof-messages.html',
})
export class ProfMessagesPage {

  _result: any;

  constructor(
  public navCtrl: NavController,
  public navParams: NavParams,
  private apiSerivce: ApiService
  ) {
  }

  getData(): void {
    this.apiSerivce.getData(
      { 
        enseignant_id : ApiService.enseignantId,
        key  : ApiService.keyToken,
      }, 'prof_messages')
        .subscribe(
            result => { 
              this._result = result;
            }, 
            error => console.log("Error :: " + error)
        )
  }

  ngOnInit(): void {
      this.getData();
  }

  nouveauMessage(){
    this.navCtrl.push(ProfNouveauMessagePage,{translation: this._result.translation});
  }
  
  detailsMessage(reference){
    reference.translation = this._result.translation;
    this.navCtrl.push(ProfConversationPage,{reference: reference});
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Messages & NotesPage');
  }
  

}



