import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, Loading, Platform, ToastController, AlertController  } from 'ionic-angular';
import {Validators, FormBuilder, FormControl,  FormGroup } from '@angular/forms';

import { MessagesPage } from '../messages';
import { ApiService } from '../../../providers/apiservice';

import { IOSFilePicker } from "@ionic-native/file-picker/ngx";
import { FileChooser } from "@ionic-native/file-chooser";
import { Camera, CameraOptions } from "@ionic-native/camera";
import { Base64 } from "@ionic-native/base64";
import { FilePath } from "@ionic-native/file-path";


@Component({
  selector: 'page-nouveau-message',
  templateUrl: 'nouveau-message.html',
})
export class NouveauMessagePage {
  public sujet 	: any;
  public translation 	: any;
  public message	: any;
  public messageRef	: any;
  private formGroup : FormGroup;
  public fileMessage: { name: any; extention: any; base64File: string };

  constructor(public navCtrl: NavController,
              public loadingCtrl: LoadingController,
              private apiSerivce: ApiService,
              public navParams: NavParams,
              private base64: Base64,
              private camera: Camera,
              private fileChooser: FileChooser,
              private plt: Platform,
              private filePicker: IOSFilePicker,
              private filePath: FilePath,
              private toastCtrl: ToastController,
              private formBuilder: FormBuilder
  ) {

    this.formGroup = this.formBuilder.group({
      sujet: ['', Validators.required],
      message: ['', Validators.required],
    });

    this.messageRef = navParams.get('messageRef');
    this.sujet = navParams.get('sujet');
    this.translation = navParams.get('translation');

  }


    ionViewDidLoad() {
      console.log('yes', this.fileMessage);
    }

    
  deleteFile() {
    this.fileMessage = null;
  }

    send()
    {

      let query = "sujet=" + this.sujet + 
                  "&message=" + this.message + 
                  "&file="+ JSON.stringify(this.fileMessage) +
                  "&eleve_id="+ApiService.eleveId + 
                  "&parent_id="+ApiService.parentId +
                  "&key="+ApiService.keyToken 
                  ;

      if(this.messageRef)
        query += "&ref="+this.messageRef;

        this.apiSerivce.postData(query,  'nouveau-message').subscribe(
          result => { 

            this.navCtrl.setRoot(MessagesPage);
    
          }, 
          error =>  {
    
            //alert(error);
            console.log("Error :: " + error);
            //this.loading.dismiss().catch();
          }
      );
    }

    chooseFile() {
      if (this.plt.is("ios")) {
        this.chooseFileForIos();
      } else {
        this.chooseFileForAndroid();
      }
    }
  
    chooseFileForIos() {
      this.filePicker
        .pickFile()
        .then(uri => {
          this.presentToast("File chosen successfully");
          this.convertToBase64(uri,false)
        })
        .catch(err => console.log("Error", err));
    }
  
    chooseFileForAndroid() {
      this.fileChooser
        .open()
        .then(uri => {
          this.presentToast("File chosen successfully");
          this.convertToBase64(uri,false)
        })
        .catch(e => {
        });
    }
  
    convertToBase64(imageUrl, isImage) {
      this.filePath
        .resolveNativePath(imageUrl)
        .then(filePath => {
          this.base64.encodeFile(filePath).then(
            (base64Fichier: string) => {
              let fileName = filePath.substring(filePath.lastIndexOf('/')+1, filePath.length);
              this.fileMessage = {
                name: fileName,
                extention: filePath.split(".").pop(),
                base64File: base64Fichier.split(",").pop()
              };
            },
            err => {
            }
          );
        })
        .catch(err => console.log(err));
    }
  
  
    presentToast(message) {
      let toast = this.toastCtrl.create({
        message: message,
        duration: 3000,
        position: 'top'
      });
    
      toast.onDidDismiss(() => {
      });
    
      toast.present();
    }

  }



