import { Component } from '@angular/core';
import { NavController, NavParams , LoadingController, Loading, Events} from 'ionic-angular';
import { ApiService } from '../../providers/apiservice';
import { ProfCoursDetailsPage } from '../prof-cours-details/prof-cours-details';
import { ProfDevoirsPage } from '../prof-devoirs/prof-devoirs';
import { MyApp } from '../../app/app.component';
import { CompteProfPage } from '../compte-prof/compte-prof';

import { Storage } from '@ionic/storage';
import { HomePage } from '../home/home';
import { ProfExamensPage } from '../prof-examens/prof-examens';
import { ProfMessagesPage } from '../prof-messages/prof-messages';

@Component({
  selector: 'page-prof-planning',
  templateUrl: 'prof-planning.html',
})
export class ProfPlanningPage {

  eleve:any;
  currentJustify = 'justified';
  results: any;
  loading: Loading;
  showModal:boolean = false;
  showModalProfil:boolean = false;
  doubleAccount:boolean = MyApp.doubleAccount;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public events: Events,
    private storage: Storage,
    private apiSerivce: ApiService,
    public loadingCtrl: LoadingController
  ) {
    this.eleve = ApiService.activeEleve;
  }

  ionViewWillEnter()
  {
   this.load();
  }

  load()
  {

    
    this.presentLoadingCustom();

    this.apiSerivce.getData(
      { 
        enseignant_id : ApiService.enseignantId,
        key  : ApiService.keyToken,
      }, 'prof_cours')
        .subscribe(
            results => {
              this.results = results;
              this.loading.dismiss();
            }
              , 
            error => console.log("Error :: " + error)
    )
  }

  next(next_date)
  {

    
    this.presentLoadingCustom();
    this.apiSerivce.getData(
      { 
        enseignant_id : ApiService.enseignantId,
        key  : ApiService.keyToken,
        next_week  : next_date
      }, 'prof_cours')
        .subscribe(
            results => {
              this.results = results;
              this.loading.dismiss();
            }
              , 
            error => console.log("Error :: " + error)
    )
  }

  prev(prev_date)
  {

    
    this.presentLoadingCustom();
    this.apiSerivce.getData(
      { 
        enseignant_id : ApiService.enseignantId,
        key  : ApiService.keyToken,
        last_week  : prev_date
      }, 'prof_cours')
        .subscribe(
            results => {
              this.results = results;
              this.loading.dismiss();
            }
              , 
            error => console.log("Error :: " + error)
    )
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  toggleModal(){
    this.showModal = !(this.showModal)?true:false;
  }

  toggleModalProfil(){
    this.showModalProfil = !(this.showModalProfil)?true:false;
  }

  coursDetails(cours) {
    this.navCtrl.push(ProfCoursDetailsPage, {
      cours : cours
    });
  }

  devoirsList() {
    this.navCtrl.push(ProfDevoirsPage, {
      type : 'devoir'      
    });
  }

  ressourcesList() {
    this.navCtrl.push(ProfDevoirsPage, {
      type : 'ressource'      
    });
  }

  moncompte() {
    this.navCtrl.push(CompteProfPage);
  }

  examensList() {
    this.navCtrl.push(ProfExamensPage);
  }

  messagerie() {
    this.navCtrl.push(ProfMessagesPage);
  }

  switchToParent() {
    this.events.publish('user:changetoparent');
  }

  logout(){


    ApiService.activeEleve = null;
    ApiService.eleveId = null;
    ApiService.enseignantId = null;

    this.storage.remove('boti_auth');
    this.storage.remove('boti_userId');
    this.storage.remove('boti_eleveId');
    this.storage.remove('boti_parentId');
    this.storage.remove('boti_enseignantId');
    this.storage.remove('boti_keyToken');
    this.storage.remove('boti_activeEleve');
    this.navCtrl.setRoot(HomePage);
  }

}



