import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';


import { ApiService } from '../../providers/apiservice';
import { NouveauMessagePage } from './nouveau-message/nouveau-message';
import { ConversationPage } from './conversation/conversation';

@Component({
  selector: 'page-messages',
  templateUrl: 'messages.html',
})
export class MessagesPage {

  _result: any;

  constructor(
  public navCtrl: NavController,
  public navParams: NavParams,
  private apiSerivce: ApiService
  ) {
  }

  getData(): void {
    this.apiSerivce.getData(
      { 
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
      }, 'messages')
        .subscribe(
            result => { 
              this._result = result;
            }, 
            error => console.log("Error :: " + error)
        )
  }

  ngOnInit(): void {
      this.getData();
  }

  nouveauMessage(){
    this.navCtrl.push(NouveauMessagePage,{translation: this._result.translation});
  }
  detailsMessage(result){
    result.translation = this._result.translation;
    this.navCtrl.push(ConversationPage,{result: result});
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Messages & NotesPage');
  }
  

}



