import {Injectable} from "@angular/core";
import {Http, Response, Headers, RequestOptions} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/Rx";

@Injectable()
export class ApiService {

    private _postsURL = "https://boti.education/p/mariefrance/botiapi/";
    static versionAr = false;
    static userId: string;
    static parentId: string;
    static enseignantId: string;
    static eleveId: string;
    static activeEleve: any;
    static keyToken: string;
    static urlLoadingPost: boolean;
    static urlLoadingGet: boolean;
    static loaderHtml: string = `
    <div class="custom-spinner-container">
      <div class="custom-spinner-box">
      <img src="assets/imgs/loading/three-dots.svg" />
      <p>Veuillez patienter...</p>
      </div>
    </div>`;
    constructor(private http: Http) {
    }

    getData(params, url): Observable<any[]> {

        return this.http
            .get(this._postsURL + url, {
                params: params } )
            .map((response: Response) => {
                return <any[]>response.json();
            })
            .catch(this.handleError);
    }

    postData(params, url): Observable<any[]> {

        if(ApiService.urlLoadingPost)
            return null;
        ApiService.urlLoadingPost = true;

        let type 	 : string	 = "application/x-www-form-urlencoded; charset=UTF-8",
        headers: any		 = new Headers({ 'Content-Type': type}),
        options: any 		 = new RequestOptions({ headers: headers });
        return this.http
            .post(this._postsURL + url, params , options )
            .map((response: Response) => {

                ApiService.urlLoadingPost = false;
                return <any[]>response.json();

            })
            .catch(this.handleError);
    }

    private handleError(error: Response) {

        ApiService.urlLoadingPost = false;
        ApiService.urlLoadingGet = false;

        console.log(error);
        return Observable.throw(error.statusText);
    }

}



