import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiService } from '../../providers/apiservice';
import { HomePage } from '../home/home';

/**
 * Generated class for the DisciplinePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

 
@Component({
  selector: 'page-discipline',
  templateUrl: 'discipline.html',
})
export class DisciplinePage {

  eleve:any;
  _result: any;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private apiSerivce: ApiService
  ) {

    this.eleve = ApiService.activeEleve;

  }

  ionViewWillEnter()
  {
    this.load();

  }

  load()
  {
    this.apiSerivce.getData(
      { 
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
      }, 'discipline')
    .subscribe(
        result => {
          this._result = result[0]
        }, 
        error => console.log("Error :: " + error)
    )
  }

  retour(){
    this.navCtrl.setRoot(HomePage);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DisciplinePage');
  }

}



