import { Component } from '@angular/core';
import { Platform, IonicPage, NavController, NavParams, ToastController, Loading, LoadingController, Slides, AlertController, App } from 'ionic-angular';
import { ViewChild } from '@angular/core';

import { ApiService } from '../../providers/apiservice';
import { SuiviPedagogiqueTabsPage } from '../suivipedagogique-tabs/suivipedagogique-tabs';
import { MyApp } from '../../app/app.component';

import { IOSFilePicker } from "@ionic-native/file-picker/ngx";
import { FileChooser } from "@ionic-native/file-chooser";
import { Camera, CameraOptions } from "@ionic-native/camera";
import { Base64 } from "@ionic-native/base64";
import { FilePath } from "@ionic-native/file-path";

import {Validators, FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'page-absences',
  templateUrl: 'absences.html',
})
export class AbsencesPage {

  @ViewChild('mySlider') slider: Slides;
  private formGroup : FormGroup;

  public selected = 0;
  public indicator = null;
  public mySlideOptions = {};

  ngAfterViewInit() 
  {
  }

  eleve:any;
  loading: Loading;
  _result: any;
  _toggleJustification:boolean = false;
  currentAbsence: any;
  imageArr: { extention: any; base64Img: string };
  motifAbsence: any;

  constructor(
  public navCtrl: NavController,
  public navParams: NavParams,
  private alertCtrl: AlertController,
  private apiSerivce: ApiService,
  private base64: Base64,
  private camera: Camera,
  private fileChooser: FileChooser,
  private plt: Platform,
  private filePicker: IOSFilePicker,
  private filePath: FilePath,
  private toastCtrl: ToastController,
  public loadingCtrl: LoadingController,
  public app: App,
  public platform: Platform,
  private formBuilder: FormBuilder
  ) {
    this.platform = platform;
    this.eleve = ApiService.activeEleve;


    this.formGroup = this.formBuilder.group({
      motif: ['', Validators.required],
    });
  }

  getData(): void {
    this.apiSerivce.getData(
      {
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
      }, 'absences')
        .subscribe(
            result => {
              this._result = result;
              this.loading.dismiss();
            }
              ,
            error => console.log("Erreur :: " + error)
        )
  }

  ngOnInit(): void {
      this.getData();
  }
  
  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  toggleJustification(absence){
    if(!this._toggleJustification) {
      this.currentAbsence = absence;
      this.motifAbsence = absence.justificationmotif;
    } else {
      this.currentAbsence = null;
    }
    this._toggleJustification = !this._toggleJustification;
  }

  submit(absence) {

    absence.image = this.imageArr;
    let query = "eleve_id="+this.eleve.id + 
                  "&parent_id="+ApiService.parentId +
                  "&key="+ApiService.keyToken +
                  "&absence="+ JSON.stringify(absence)
                  ;

    this.apiSerivce.postData(query, 'absences').subscribe(
      result => {
        let res: any = result;
        this.app.getRootNav().setRoot(SuiviPedagogiqueTabsPage, {
          tabIndex : 1
        });
        let alert = this.alertCtrl.create({
          cssClass: 'success_alert_boti',
          title: res.title,
          message: res.message,
          buttons: [{
            text: 'Fermer',
            role: 'cancel',
            handler: () => {
            }
          }]
        });
        alert.present();
      }, 
      error =>  {
        this.loading.dismiss();
      }
  );
  }

checkJustification() {
  this._toggleJustification = false;
  this.currentAbsence.justificationmotif = this.motifAbsence;
  this.submit(this.currentAbsence);
  this.currentAbsence = null;
  this.motifAbsence = null;
}


convertToBase64(imageUrl, isImage) {
  this.filePath
    .resolveNativePath(imageUrl)
    .then(filePath => {
      this.base64.encodeFile(filePath).then(
        (base64Fichier: string) => {
          this.imageArr = {
            extention: filePath.split(".").pop(),
            base64Img: base64Fichier.split(",").pop() //same comment for image follows here.
          }
        },
        err => {
        }
      );
    })
    .catch(err => console.log(err));
}


presentToast(message) {
  let toast = this.toastCtrl.create({
    message: message,
    duration: 3000,
    position: 'top'
  });

  toast.onDidDismiss(() => {
  });

  toast.present();
}

openGallery() {
  var options = {
    sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
    destinationType: this.camera.DestinationType.FILE_URI
  };
  this.camera
    .getPicture(options)
    .then(imageData => {
      this.presentToast("Image chosen successfully");
      this.convertToBase64(imageData, true);
    })
    .catch(e => {
    });
}

  select(index) 
  {
    this.selected = index;
    this.slider.slideTo(index, 500);
  }

  onSlideChanged($event) 
  {
    if (((($event.touches.startX - $event.touches.currentX) <= 100) || (($event.touches.startX - $event.touches.currentX) > 0)) && (this.slider.isBeginning() || this.slider.isEnd())) 
    {
      //console.log("interdit Direction");
    }
    else 
    {
      //console.log("OK Direction");
      //this.indicator.style.webkitTransform = 'translate3d(' + (-($event.translate) / 4) + 'px,0,0)';
    }

  }

  panEvent(e) 
  {
    let currentIndex = this.slider.getActiveIndex();
    console.log('here' + currentIndex);
    if (currentIndex === 1) 
    {
      this.selected = 1;
      //this.indicator.style.webkitTransform = 'translate3d(100%,0,0)';
    }
    if (currentIndex === 0) 
    {
      this.selected = 0;
      //this.indicator.style.webkitTransform = 'translate3d(0%,0,0)';
    }
  }


}



