import { Component , ElementRef, ViewChild, NgZone} from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, Loading, Platform, ToastController, AlertController  } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { DatepickerOptions } from 'ng2-datepicker';
import {Validators, FormBuilder, FormGroup } from '@angular/forms';

import { ApiService } from '../../providers/apiservice';
import { IContent } from '../../providers/icontent';
import { ProfPlanningPage } from '../prof-planning/prof-planning';

import { IOSFilePicker } from "@ionic-native/file-picker/ngx";
import { FileChooser } from "@ionic-native/file-chooser";
import { Camera, CameraOptions } from "@ionic-native/camera";
import { Base64 } from "@ionic-native/base64";
import { FilePath } from "@ionic-native/file-path";

@Component({
  selector: 'page-prof-devoir-form',
  templateUrl: 'prof-devoir-form.html',
})


export class ProfDevoirFormPage {
  
  fileArray: { extention: any; base64File: string };
  imageArr: { extention: any; base64Img: string };

  loading: Loading;
  _result: any;
  _resultPage: any;
  image: any;
  file: any;
  
  content: IContent = {} as IContent ;
  private formGroup : FormGroup;

  constructor(
      public navCtrl: NavController,
      public navParams: NavParams,
      private apiSerivce: ApiService,
      private alertCtrl: AlertController,
      private base64: Base64,
      private camera: Camera,
      private fileChooser: FileChooser,
      private plt: Platform,
      private filePicker: IOSFilePicker,
      private filePath: FilePath,
      private toastCtrl: ToastController,
      public loadingCtrl: LoadingController, 
      private _ngZone: NgZone,
      private formBuilder: FormBuilder
  ) {

    if(this.navParams.get('content')){
     this.content = this.navParams.get('content');
    }
    else{
      this.content.id = null;
      this.content.type = this.navParams.get('type');
    }
    

    this.formGroup = this.formBuilder.group({
        titre: ['', Validators.required],
        classes: ['', Validators.nullValidator],
        matiere: ['', Validators.nullValidator],
        image: ['', Validators.nullValidator],
        file: ['', Validators.nullValidator],
        description: ['', ( this.content.type == 'devoir' ) ? Validators.required : Validators.nullValidator],
        date: ['', ( this.content.type == 'devoir' ) ? Validators.required : Validators.nullValidator],
        id: ['', Validators.nullValidator]
      });

  }

  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  presentLoadingCustom() {
    this.loading = this.loadingCtrl.create({
    spinner: 'hide',
    content: ApiService.loaderHtml,
    duration: 3000000
  });
  this.loading.present();
}
  
  getData(): void {

    let params = {
      op : 'prof-content',
      enseignant_id : ApiService.enseignantId,
      type : this.content.type,
      add : this.content.id?false:true
    };

    this.apiSerivce.getData(params, 'prof_content')
        .subscribe(
            result =>  {
                this._resultPage = result;
                this.loading.dismiss();
            },
            error => {
              this.loading.dismiss();
              console.log("Error :: " + error);
            }
        )
}

ngOnInit(): void {
    this.getData();
}
showToast(message: string) {
  let toast = this.toastCtrl.create({
    message: message,
    duration: 2000,
    position: 'middle'
  });

  toast.present(toast);
}

submit() {
    this.presentLoadingCustom();
    this.content.file = this.fileArray;
    this.content.image = this.imageArr;
    this.apiSerivce.postData("enseignant_id="+ApiService.enseignantId+"&image="+this.image+"&content=" + JSON.stringify(this.content), 'prof_content').subscribe(
      result => {
        this.loading.dismiss(); 
        let res: any = result;
        this.navCtrl.setRoot(ProfPlanningPage);
        let alert = this.alertCtrl.create({
          cssClass: 'success_alert_boti',
          title: res.title,
          message: res.message,
          buttons: [{
            text: 'Fermer',
            role: 'cancel',
            handler: () => {
            }
          }]
        });
        alert.present();

      }, 
      error =>  {
        this.loading.dismiss();
      }
  );
  }

  openGallery() {
    var options = {
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      destinationType: this.camera.DestinationType.FILE_URI
    };
    this.camera
      .getPicture(options)
      .then(imageData => {
        this.presentToast("Image chosen successfully");
        this.convertToBase64(imageData, true);
      })
      .catch(e => {
      });
  }

  chooseFile() {
    if (this.plt.is("ios")) {
      this.chooseFileForIos();
    } else {
      this.chooseFileForAndroid();
    }
  }

  chooseFileForIos() {
    this.filePicker
      .pickFile()
      .then(uri => {
        this.presentToast("File chosen successfully");
        this.convertToBase64(uri,false)
      })
      .catch(err => console.log("Error", err));
  }

  chooseFileForAndroid() {
    this.fileChooser
      .open()
      .then(uri => {
        this.presentToast("File chosen successfully");
        this.convertToBase64(uri,false)
      })
      .catch(e => {
      });
  }

  convertToBase64(imageUrl, isImage) {
    this.filePath
      .resolveNativePath(imageUrl)
      .then(filePath => {
        this.base64.encodeFile(filePath).then(
          (base64Fichier: string) => {
            if (isImage == false) {
              this.fileArray = {
                extention: filePath.split(".").pop(),
                base64File: base64Fichier.split(",").pop() //split(",").pop() depends on the requirement, if back end API is able to extract
                //the file mime type then you can do this, if back end expects  UI update the Mime type
                //  then don't use split(",").pop()
              };
            } else {
              this.imageArr = {
                extention: filePath.split(".").pop(),
                base64Img: base64Fichier.split(",").pop() //same comment for image follows here.
              }
            }
          },
          err => {
          }
        );
      })
      .catch(err => console.log(err));
  }


  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });
  
    toast.onDidDismiss(() => {
    });
  
    toast.present();
  }


  uploadImageAndFile(){

  }
  
  
}



