import { Component } from '@angular/core';
import { NavController, ViewController, Loading, NavParams, ToastController, LoadingController } from 'ionic-angular';
import {Validators, FormBuilder, FormGroup } from '@angular/forms';

import { ApiService } from '../../providers/apiservice';

@Component({
  selector: 'page-change-password',
  templateUrl: 'change-password.html'
})
export class ChangePasswordPage {


  loading: Loading;
  loadingBlur: any;
  formGroup : FormGroup;
  actuel : any;
  nouveau : any;
  confirmer : any;
  _result: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public toastCtrl : ToastController,
    private apiSerivce: ApiService,
    public loadingCtrl: LoadingController,
    private formBuilder: FormBuilder,
    public viewCtrl: ViewController
  ) {

    this.formGroup = this.formBuilder.group({
      actuel: ['', Validators.required],
      nouveau: ['', Validators.required],
      confirmer: ['', Validators.required],
    });

  }

  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  getData(): void {
    this.apiSerivce.getData({
       parent_id : ApiService.parentId,
       key  : ApiService.keyToken,
      }, 'change-password')
        .subscribe(
            result => { 
              this._result = result;

              this.loading.dismiss();
              this.loadingBlur = null;
            }, 
            error =>  {
              console.log("Error :: " + error);
              this.loading.dismiss();
            }
        )
  }

  ngOnInit(): void {
    this.getData();
  }

  closeModal() {
    this.viewCtrl.dismiss();
  }

  valider() {

    let query = "actuel=" + this.actuel + 
                  "&nouveau=" + this.nouveau + 
                  "&confirmer=" + this.confirmer + 
                  "&parent_id="+ApiService.parentId +
                  "&key="+ApiService.keyToken 
                  ;
                  
    this.apiSerivce.postData(query, 'change-password').subscribe(
        result => { 
      
          this._result = result;

          if(this._result.error === false)
          {

            this.sendNotification(this._result.msg, 'toast-success');
            this.viewCtrl.dismiss();

          }
          else
          {
            this.sendNotification(this._result.msg, 'toast-error');
            this.actuel = null;
            this.nouveau = null;
            this.confirmer = null;
          }

        }, 
        error =>  {

        }
    );
  }

  sendNotification(message, classe)  : void
  {
     let notification = this.toastCtrl.create({
         message 		: message,
         duration 		: 100000,
         cssClass: classe,
		 position: 'middle',
		 showCloseButton: true,
		 closeButtonText :'Ok'
     });
     notification.present();
  }

  

}



