import { Component } from '@angular/core';
import { NavController, LoadingController, Loading, Events } from 'ionic-angular';

import { ApiService } from '../../providers/apiservice';
import { MyApp } from '../../app/app.component';
import { NouveautesPage } from '../nouveautes/nouveautes';
import { CompteElevePage } from '../compte-eleve/compte-eleve';
import { CompteParentPage } from '../compte-parent/compte-parent';
import { ChangePasswordPage } from '../change-password/change-password';

import { Storage } from '@ionic/storage';

@Component({
  selector: 'page-compte',
  templateUrl: 'compte.html',
})
export class ComptePage {

  eleve:any;
  loading: Loading;
  loadingBlur: any;
  _result: any;
  constructor(
    public navCtrl: NavController,
    private apiSerivce: ApiService,
    public events: Events,
    private storage: Storage,
    public loadingCtrl: LoadingController
  ) {

    this.eleve = ApiService.activeEleve;
  }

  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

  getData(): void {
    this.apiSerivce.getData({
       parent_id : ApiService.parentId,
       key  : ApiService.keyToken,
      }, 'compte')
        .subscribe(
            result => { 
              this._result = result;
              console.log(this._result, 'yes');
              this.events.publish('storage:infos', this._result);

              this.loading.dismiss();
              this.loadingBlur = null;
            }, 
            error =>  {
              console.log("Error :: " + error);
              this.loading.dismiss();
            }
        )
  }

  changeprofil(eleve) {

    ApiService.activeEleve = eleve;
    ApiService.eleveId = eleve.id;

    this.storage.set('activeleve',eleve);
    this.storage.set('boti_eleveId', eleve.id);
    this.storage.set('boti_activeEleve', eleve);

    MyApp.eleve = eleve;

    this.navCtrl.setRoot(NouveautesPage);

  }

  ngOnInit(): void {
      this.getData();
  }

  compteEleve(eleve) {
    this.navCtrl.push(CompteElevePage, {
      eleve: eleve
    });
  }
  
  changePassword() {
    this.navCtrl.push(ChangePasswordPage);
  }

  compteParent(parent) {
    this.navCtrl.push(CompteParentPage, {
      parent: parent
    });
  }

}



