import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiService } from '../../providers/apiservice';

import { HomePage } from '../home/home';
import { CoursPage } from '../cours/cours';
import { AbsencesPage } from '../absences/absences';
import { ExamensPage } from '../examens/examens';
import { DisciplinePage } from '../discipline/discipline';

@Component({
  selector: 'page-suivipedagogique-tabs',
  templateUrl: 'suivipedagogique-tabs.html',
})
export class SuiviPedagogiqueTabsPage {
  tab1Root = CoursPage;
  tab2Root = AbsencesPage;
  tab3Root = ExamensPage;
  tab4Root = DisciplinePage;
  tabIndex = 0;
  eleve:any;
  translation:any;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private apiSerivce: ApiService,
  ) {
    this.eleve = ApiService.activeEleve;
    
    if(navParams.get('tabIndex')) {
      this.tabIndex = navParams.get('tabIndex');
    }
    if(!ApiService.versionAr){
      this.translation = {
        'title' : 'Suivi pédagogique',
        'tab1' : 'Emploi du temps',
        'tab2' : 'Absences',
        'tab3' : 'Examens & notes',
        'tab4' : 'Discipline',
      };

    } else {

      this.translation = {
        'title' : 'التتبع البيداغوجي',
        'tab1' : 'استعمال الزمن',
        'tab2' : 'الغياب',
        'tab3' : 'الامتحانات و النقط',
        'tab4' : 'السلوك',
      };

    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MenuTabsPage');
  }

  

}



