import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, Loading, Platform, ToastController, AlertController  } from 'ionic-angular';
import { ViewChild } from '@angular/core';
import { DomSanitizer} from '@angular/platform-browser';
import { InAppBrowser } from '@ionic-native/in-app-browser';

import { IOSFilePicker } from "@ionic-native/file-picker/ngx";
import { FileChooser } from "@ionic-native/file-chooser";
import { Camera, CameraOptions } from "@ionic-native/camera";
import { Base64 } from "@ionic-native/base64";
import { FilePath } from "@ionic-native/file-path";


import { File } from '@ionic-native/file';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { AndroidPermissions } from '@ionic-native/android-permissions';

import { ApiService } from '../../providers/apiservice';

@Component({
  selector: 'page-devoirs',
  templateUrl: 'devoirs.html',
})
export class DevoirsPage {

  ngAfterViewInit() 
  {
  }

  eleve:any;
  loading: Loading;
  _result: any;
  _resultFile: any;
  private fileTransfer: FileTransferObject;
  public fileFormat: { name: any; extention: any; base64File: string };

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private alertCtrl: AlertController,
    private apiSerivce: ApiService,
    public loadingCtrl: LoadingController,
    private base64: Base64,
    private camera: Camera,
    private fileChooser: FileChooser,
    private filePicker: IOSFilePicker,
    private filePath: FilePath,
    private toastCtrl: ToastController,
    private sanitizer: DomSanitizer,
    private iab: InAppBrowser,
    public platform: Platform,
    private file: File,
    private androidPermissions: AndroidPermissions,
    private transfer: FileTransfer
  ) {
    this.platform = platform;
    this.eleve = ApiService.activeEleve;
  }

  getData(): void {
    this.apiSerivce.getData(
      {
        parent_id : ApiService.parentId,
        eleve_id : ApiService.eleveId,
        key  : ApiService.keyToken,
      }, 'devoirs_date_v2')
        .subscribe(
            result => {
              this._result = result;
              this.loading.dismiss();
            }
              ,
            error => console.log("Erreur :: " + error)
        )
  }

  getInnerHTMLValue(result){
    return this.sanitizer.bypassSecurityTrustHtml(result.description);
  }


  public downloadFile(fileName, filePath) {
    //here encoding path as encodeURI() format.  
    let url = encodeURI(filePath);  
    
    this.fileTransfer = this.transfer.create();
    
    let directory = '';
    if (this.platform.is('cordova')) {
      directory = this.file.externalRootDirectory + '/Download/';
    } else {
      directory = this.file.documentsDirectory;
    }


    this.androidPermissions.requestPermissions([
        this.androidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE
    ]).then((result) => {
      // here iam mentioned this line this.file.externalRootDirectory is a native pre-defined file path storage. You can change a file path whatever pre-defined method.  
      this.fileTransfer.download(url, directory  + fileName, true).then((entry) => {  
        //here logging our success downloaded file path in mobile.  
        const alertSuccess = this.alertCtrl.create({
          title: `Téléchargement réussi !`,
          cssClass: 'notification-alert',
          message: `${fileName} a été téléchargé avec succès sur : ${entry.toURL()}`,
          buttons: ['Ok']
        });
    
        alertSuccess.present();

    }, (error) => {  
        //here logging our error its easier to find out what type of error occured.  
        console.log('download failed: ' + error);  
    });  
    });
    
  } 

  ngOnInit(): void {
      this.getData();
  }
  
  ionViewDidLoad() {
    this.presentLoadingCustom();
  }

  marquerFait(devoir) {
    this.send_file(devoir);
  }

  deleteFile(devoir, file) {
    const index: number = devoir.devoir_fait.files.indexOf(file);
    if (index !== -1) {
      devoir.devoir_fait.files.splice(index, 1);
    }  
  }

send_file(devoir) {
  let query = "&eleve_id="+this.eleve.id + 
              "&parent_id="+ApiService.parentId +
              "&key="+ApiService.keyToken+
              "&devoir="+JSON.stringify({id: devoir.id, files: devoir.devoir_fait.files})
              ;

    let loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
    });
  
    loading.present();
	this.apiSerivce.postData(query, 'devoirs_date_v2').subscribe(
		result => {
       
      let res: any = result;
      if(res.file_sent){
        devoir.devoir_fait.file_sent = true;
        console.log(devoir);
      } else {
        devoir.devoir_fait.fait = true;
      }
      loading.dismiss();
      let alert = this.alertCtrl.create({
        cssClass: 'success_alert_boti',
        title: res.title,
        message: res.message,
        buttons: [{
          text: 'Fermer',
          role: 'cancel',
          handler: () => {
          }
        }]
      });
      alert.present();
		}, 
		error =>  {
		  this.loading.dismiss();
		}
	);
}


  chooseFile(devoir) {
    if (this.platform.is("ios")) {
      this.chooseFileForIos(devoir);
    } else {
      this.chooseFileForAndroid(devoir);
    }
  }

  chooseFileForIos(devoir) {
    this.filePicker
      .pickFile()
      .then(uri => {
        this.presentToast("File chosen successfully");
        this.convertToBase64(devoir, uri,false)
      })
      .catch(err => console.log("Error", err));
  }

  chooseFileForAndroid(devoir) {
    this.fileChooser
      .open()
      .then(uri => {
        this.presentToast("File chosen successfully");
        this.convertToBase64(devoir, uri,false)
      })
      .catch(e => {
      });
  }

  convertToBase64(devoir, imageUrl, isImage) {
    this.filePath
      .resolveNativePath(imageUrl)
      .then(filePath => {
        this.base64.encodeFile(filePath).then(
          (base64Fichier: string) => {
            let fileName = filePath.substring(filePath.lastIndexOf('/')+1, filePath.length);
            this.fileFormat = {
              name: fileName,
              extention: filePath.split(".").pop(),
              base64File: base64Fichier.split(",").pop()
            };
            devoir.devoir_fait.files.push(this.fileFormat);
            this.fileFormat = null;
          },
          err => {
          }
        );
      })
      .catch(err => console.log(err));
  }


  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });
  
    toast.onDidDismiss(() => {
    });
  
    toast.present();
  }

  presentLoadingCustom() {
      this.loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: ApiService.loaderHtml,
      duration: 10000
    });
    this.loading.present();
  }

}



